}
#log {
    color: #7a7a7a;
    border-radius: 3px;
    margin-bottom: 15px;
    background: #fff;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.3);
    padding: 30px;
}
.login-form .btn {        
    font-size: 20px;
    font-weight: bold;
    background: #40a0c0;
    border: none;
    outline: none !important;
    padding: 2%;
    width: 50%;
    border-radius: 20px;
    margin-left: 25%;
    margin-top:3%;
    box-shadow: 0 4px 6px -1px rgba(99, 98, 98, 0.8);
}
.login-form .btn:hover, .login-form .btn:focus {
    background: #0b5e96;
}
.login-form a {
    color: #fff;
    text-decoration: underline;
}
.login-form a:hover {
    text-decoration: none;
}
.login-form form a {
    color: #7a7a7a;
    text-decoration: none;
}
.login-form form a:hover {
    text-decoration: underline;
}

