.search {
  margin: 200px 293px 293px 293px;
  vertical-align: center;
  align-items: center;
}

.search > h3 {
  font-weight: normal;
}

.search > h1,
.search > h3 {
  color: white;
  margin-bottom: 20px;
  text-shadow: 0 1px #0091c2;
}

.search > div {
  display: inline-block;
  position: relative;
}

.search > div:after {
  content: "";
  background: white;
  width: 4px;
  height: 22px;
  position: absolute;
  top: 41px;
  right: 3px;
  transform: rotate(135deg);
  border-radius: 50px;
}

.search > div > input {
  color: white;
  font-size: 16px;
  background: transparent;
  width: 25px;
  height: 25px;
  padding: 20px;
  border: solid 3px white;
  outline: none;
  border-radius: 35px;
  box-shadow: 0 1px #0091c2;
  transition: width 0.5s;
}

.search > div > input::-webkit-input-placeholder {
  color: #efefef;
  opacity: 1;
}

.search > div > input:-ms-input-placeholder {
  color: #efefef;
  opacity: 1;
}

.search > div > input::-ms-input-placeholder {
  color: #efefef;
  opacity: 1;
}

.search > div > input::placeholder {
  color: #efefef;
  opacity: 1;
}

.search > div > input::-ms-placeholder {
  color: #efefef;
}

.search > div > input::-ms-input-placeholder {
  color: #efefef;
}

.search > div > input:focus,
.search > div > input:valid {
  width: 600px;
}

